/* Шрифт Oswald */
/* Общие стили */
body {
  height: 100%;
  font: 13px/16px Arial, Tahoma, sans-serif;
  color: #000;
  background: #fff url(../images/bg.jpg) repeat; }

ol, ul {
  list-style: none; }

* {
  margin: 0;
  padding: 0;
  outline: none; }

a, a:hover {
  color: #000;
  text-decoration: none; }

/* Отступы */
.clearfix:before, .clearfix:after {
  content: " ";
  display: table; }

.clearfix:after {
  clear: both; }

.left {
  float: left; }

.right {
  float: right; }

.text-right {
  text-align: right; }

.text-center {
  text-align: center; }

.center {
  width: 960px;
  margin: 0 auto; }

/* Обвёртка */
#wrapper {
  min-height: 100%;
  height: auto !important;
  height: 100%; }

.middle {
  padding-bottom: 295px; }

/* Индивидуальные стили сайта */
/* Панель в шапке */
.panel-top {
  height: 34px;
  background-color: #2a2b2c;
  border-top: 3px solid #cc3333; }

/* Социальные сети */
.social {
  float: left;
  padding: 4px 0; }
  .social a {
    display: inline-block;
    height: 23px;
    width: 23px;
    vertical-align: middle;
    background: url(../images/sprite-social.png) no-repeat center;
    border-radius: 50%; }
    .social a.s-fb {
      background-position: 0 0; }
    .social a.s-dribble {
      background-position: -23px 0; }
    .social a.s-tw {
      background-position: -46px 0; }
    .social a.s-mail {
      background-position: -69px 0; }
    .social a.s-vimeo {
      background-position: -92px 0; }

/* Вход на сайт */
.login {
  padding-top: 7px; }
  .login a, .login span {
    color: #fff; }
  .login a:hover {
    text-decoration: underline; }

/* Корзинка */
.basket {
  float: right;
  width: 100px;
  height: 45px;
  border-radius: 0 0 10px 10px;
  background: #cc3333 url(../images/basket.png) no-repeat 5px 6px;
  margin-left: 25px;
  overflow: hidden; }
  .basket a {
    display: block;
    height: 45px;
    width: 100px;
    color: #fff;
    padding-top: 13px;
    padding-left: 40px; }

/* Шапка сайта */
#header {
  padding: 18px 0; }

/* Логотип */
#logo img, #logo span {
  display: inline-block;
  vertical-align: middle;
  font: normal 30px/30px arial, sans-serif; }

/* Навигация */
.nav {
  margin-top: 25px; }
  .nav li {
    position: relative;
    float: left; }
    .nav li + li {
      margin-left: 30px; }
    .nav li a {
      font: bold 14px arial, sans-serif;
      text-transform: uppercase; }
    .nav li:before {
      content: "";
      position: absolute;
      top: -17px;
      left: 50%;
      margin-left: -18px;
      display: none;
      width: 35px;
      border-top: 4px solid #efc539; }
    .nav li:hover:before, .nav li.active:before {
      display: block; }

/* HOME */
#home {
  background: #fff;
  margin-bottom: 40px;
  height: 100px;
  border-bottom: 1px solid #e0e0e0; }

.home-bg {
  background-color: #cd3737;
  padding: 20px 0;
  box-shadow: inset 0 5px 5px rgba(0, 0, 0, 0.25); }

.title-home {
  font: normal 28px 'Oswald', sans-serif;
  color: #fff;
  text-transform: uppercase;
  text-shadow: 0 1px 1px rgba(0, 0, 0, 0.25); }

/* Хлебные крошки */
.breadcrumbs li {
  float: left;
  font: 12px/10px arial, sans-serif;
  color: #fff;
  text-shadow: 0 1px 1px rgba(0, 0, 0, 0.25); }
  .breadcrumbs li a {
    font: 12px/10px arial, sans-serif;
    color: #fff;
    text-shadow: 0 1px 1px rgba(0, 0, 0, 0.25); }
  .breadcrumbs li .sp {
    display: inline-block;
    margin-left: 3px;
    margin-right: 5px;
    font-size: 16px; }
  .breadcrumbs li:hover {
    text-decoration: underline; }

/** Хлебные крошки **/
.link-home {
  display: inline-block;
  margin-top: 8px;
  width: 36px;
  height: 33px;
  background: url(../images/home.png) no-repeat; }
